@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
    background-color: white;
    font-family: 'Libre Franklin', sans-serif;
    font-size: 25px;

}

.logo{
    color:  hsl(209, 33%, 12%);
    font-size: 0.7em;
    margin-top: 100px;

}

.logo .stop{
    color: hsl(223, 87%, 63%);
}

.tittle h1{
    color:  hsl(0, 0%, 59%);
    font-weight: 300;
    margin-bottom: 0;
  

}

.tittle span{
    color:  hsl(209, 33%, 12%);
    font-weight: 700;
}

.para{
    font-weight: 300;
    color: black;
    font-size: 0.8em;
    margin-top: -5px;
}
.form{
    margin-top: 70px;
    width: 100%;


}
form{
 
        display: -webkit-flex;
 
        display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
         -webkit-justify-content: center;
                 justify-content: center;

}
form div{
    width: 30%;
    text-align: center;
}
.form form input{
    border-radius: 50px;
    border: 0.9px solid  hsl(0, 0%, 59%);
    width: 80%;
    height: 20px;
    outline: none;
    padding: 18px 35px;
    font-size: 0.65em;
    background-color: white;
     color:  hsl(209, 33%, 12%);
     opacity: 0.7;
}

.form button{
    border-radius: 50px;
    border: none;
    background-color: hsl(223, 87%, 63%);
    color: white;
        font-size: 0.8em;
    margin-left: 15px;
    padding: 17px 60px;
    height: 50%;
}

.photo img{
    width: 50%;
    margin-top: 80px;
}

.contact{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;

}
.contact .social{

    border-radius: 50px;
    border: 1px solid hsl(0, 0%, 59%);
    padding: 10px;
    margin-top: 80px;
    fill:hsl(223, 87%, 63%) ;
}

.contact a:not(:nth-of-type(1)){
    margin-left: 30px;
}

.copyright{
    font-size: 0.7em;
color:  hsl(0, 0%, 59%);
}


footer{
    display: none;
}


.contact .social:hover{
    background-color:hsl(223, 87%, 63%) ;
    fill:white ;
    border: 1px solid white;

}

.error{
    color: red;
    font-size: 0.55em;
    text-align: left;
        visibility: hidden;
        width: 50%;
        margin-left: 2em;

}

.errorclose{
    visibility: visible !important;
}

.redline{
        border: 0.9px solid red !important;

}

@media screen and (max-width: 950px) {
        form{
            -webkit-flex-direction: column;
                    flex-direction: column;
            display: -webkit-flex;
            display: flex;
            -webkit-align-items: center;
                    align-items: center;
        }

        form div{
            width: 80%;
        }
       
        .tittle h1{
            font-size: 8vw;
        }
        input{
            width: 95% !important;
            padding: 0 !important;
            padding-left: 5% !important;
            height: 56px !important;  
                border: 0.9px solid hsl(223, 87%, 63%) !important;
      
        }
        input::-webkit-input-placeholder{
            margin: 10px 30px ;
        }
        input:-ms-input-placeholder{
            margin: 10px 30px ;
        }
        input::placeholder{
            margin: 10px 30px ;
        }
        button{
            width: 80% !important;
            margin-left: 0 !important;
            margin-top: 25px;
        }
        .photo img{
            width: 75%;
        }
        .error{
            visibility: visible;
            display: none;
            width: 80%;
        }

        .errorclose{
            display: block;
        }

        .contact, .copyright{
            zoom: 75%;
        }

    }
